<template>
  <div>
    <h1>hello inter component</h1>
    <h2>{{ txt }}</h2>
    <h3>input style for module : {{ module }}</h3>
    <input type="text" v-model="module" />
    <br />
    <br />
    {{ total }}
    <br />
    <br />
    <button @click="exChange()">Change</button>
    <br />
    <h1>props info</h1>
    <ul>
      <li>N:{{ name }}</li>
      <li>score:{{ score }}</li>
      <li>Amount:{{ amount }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    score: Number,
    amount: Number
  },
  data () {
    return {
      txt: 1234,
      module: ''
    }
  },
  methods: {
    exChange () {
      this.txt = 12345678
    }
  },
  computed: {
    total () {
      return this.module * 2
    }
  }
}
</script>
