<!--空氣汙染練習-->
<template>
  <div>
    <p>要用到元件。</p>
    <div class="alert alert-secondary">
      <p>需求：</p>
      <ol class="mb-0">
        <li>將資料內容透過 "元件" 呈現</li>
        <li>製作城市過濾選項</li>
        <li>將內層資料透過 emit 傳遞到外層 (作為另一個關注城市的呈現)</li>
        <li>依據不同污染呈現不同色彩</li>
        <li>加分題：透過 localStorage 儲存上次關注的城市</li>
      </ol>
    </div>

    <div id="app">
      <select name="" id="" class="form-control mb-3" @change="getCounty">
        <option value="">----請選擇城市----</option>
        <option :value="item" v-for="(item, i) in location" :key="i">
          {{ item }}
        </option>
      </select>
      <div>
        <h4>我的最愛城市</h4>
        <div class="WeatherCard d-flex flex-wrap">
          <Weatherinfo
            v-for="(item, i) in stared"
            :key="i"
            :deta="item"
            @getlove="getstar"
            class="col-4"
          ></Weatherinfo>
        </div>
      </div>
      <hr>
      <div>
        <h4>選擇城市</h4>
        <div class="WeatherCard d-flex flex-wrap">
          <Weatherinfo
            v-for="(item, i) in filterData"
            :key="i"
            :deta="item"
            @getlove="getstar"
            class="col-4"
          ></Weatherinfo>
        </div>
      </div>
      <hr />
    </div>
  </div>
</template>

<script>
import Weatherinfo from './Weatherinfo.vue'
export default {
  components: {
    Weatherinfo
  },
  data () {
    return {
      data: [],
      newData: [],
      location: [],
      filterData: [],
      stared: JSON.parse(localStorage.getItem('siteName')) || []
    }
  },
  methods: {
    getData () {
      const vm = this
      const api = 'http://opendata2.epa.gov.tw/AQI.json'
      this.$http.get(api).then((response) => {
        console.log(response)
        vm.data = response.data
        console.log(vm.data)
        vm.newData = vm.data
        vm.filterData = vm.data
        var locationArray = []
        vm.data.forEach((item) => {
          locationArray.push(item.County)
        })
        vm.location = locationArray.filter(function (element, index, array) {
          // console.log(index, 'element', element)
          // console.log(array.indexOf(element), index)
          return array.indexOf(element) === index
        })
        console.log(vm.location)
      })
    },
    getCounty (e) {
      const vm = this
      if (e.target.value === '') {
        vm.filterData = vm.data
      } else {
        vm.filterData = []
        vm.filterData = vm.newData.filter((item) => {
          const filter = e.target.value
          // console.log(filter)
          // console.log(item.County)
          return item.County === filter
        })
        console.log(vm.filterData)
      }
    },
    getstar (e) {
      const vm = this
      if (vm.stared.indexOf(e) === -1) {
        vm.stared.push(e)
      } else {
        vm.stared.splice(vm.stared.indexOf(e), 1)
      }
      localStorage.setItem('siteName', JSON.stringify(vm.stared))
    }
  },
  created () {
    this.getData()
  }
}
</script>
