<template>
  <div>
    <div class="card m-3 ">
      <div class="card-header" >
        {{ deta.County }} - {{ deta.SiteName }}
        <button class="float-right" @click.prevent="starlove"> 最愛 </button>
      </div>
      <div class="card-body" :class="statusColor">
        <ul class="list-unstyled">
          <li>AQI 指數: {{ deta.AQI }}</li>
          <li>PM2.5: {{ deta["PM2.5"] }}</li>
          <li>說明: {{ deta.Status }}</li>
        </ul>
        <span class="font-weight-bold"> Time:{{ deta.PublishTime }} </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['deta'],
  methods: {
    starlove () {
      const vm = this
      vm.$emit('getlove', this.deta)
    }
  },
  computed: {
    statusColor: function () {
      const level = [
        '良好',
        '普通',
        '對敏感族群不健康',
        '對所有族群不健康',
        '非常不健康',
        '危害'
      ]
      const cssColor = [
        'status-aqi1',
        'status-aqi2',
        'status-aqi3',
        'status-aqi4',
        'status-aqi5',
        'status-aqi6'
      ]
      return cssColor[level.indexOf(this.deta.Status)]
    }
  }
}
</script>
<style>
.status-aqi1 {
  background-color: lightgreen;
}
.status-aqi2 {
  background-color: #ffff00;
}
.status-aqi3 {
  background-color: #ff7e00;
}
.status-aqi4 {
  background-color: #ff0000;
}
.status-aqi5 {
  background-color: #8f3f97;
}
.status-aqi6 {
  background-color: #7e0023;
}
</style>
